body{
    margin: 0;
    background-color: #056679;
}
.todo-cont{
    width: 350px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
    font: 1em sans-serif;
}
.input-sec{
    background-color: #ececec;
    padding: 18px;
    border-radius: 10px;
}
h1{
    margin: 0;
    margin-bottom: 15px;
    color: #ababab;
}
.input-sec input{
border-radius: 5px;
border: 1px solid silver;
width: 100%;
padding: 10px;
box-sizing: border-box;
color: gray;
}
input:focus {
    outline:none;
}
input::placeholder{
    color: silver;
}

ul{
   
 text-align: left;
 padding: 0;
    
}
ul li{
    display: flex;
    list-style: none;
    padding: 15px;
    background-color: whitesmoke;
    border-radius: 5px;
    color: gray;
    margin-bottom: 10px;
}
ul li i{
    text-align: right;
    flex: 1;
}
